.HQcontainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}
.HQtopContainer {
  height: calc(100% - calc(43vh + 50px));
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  flex-shrink: 0;
  align-items: center;
  justify-content: space-evenly;
  padding: 15px 45px 15px 55px;
  background-color: rgba(255, 255, 255, 0.03);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.HQconsole {
  margin: 0px 35px 0px 0px;
  width: 45vw;
  height: 90%;
}
.HQuiControls {
  width: 45vw;
  max-width: 550px;
  height: 90%;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  border-radius: 5px;
}
.HQtimelineBox {
  width: 100%;
  padding: 5px 20px 10px 5px;
  display: block;
  flex-wrap: nowrap;
  flex-shrink: 0;
  height: 43vh;
  position: absolute;
  bottom: 0px;
}
.HQtimelineButtonRow {
  padding: 5px 20px 5px 55px;
  flex-wrap: nowrap;
}
.HQtimelineButtonUnit {
  padding: 0px 12px 0px 12px;
}
.HQbuttonText {
  color: rgba(255, 255, 255, 0.65);
  font-size: 1.2em;
  /*left   : -15px;*/
}
@media only screen and (max-width: 800px) {
  .HQcontainer {
    position: static;
    justify-content: flex-start;
  }
  .HQtopContainer {
    padding: 10px 0px 10px 0px;
    justify-content: center;
  }
  .HQuiControls {
    width: 100%;
  }
  .HQtimelineButtonRow {
    padding: 0px 5px 5px 10px;
  }
  .HQtimelineButtonUnit {
    padding: 0px 0px 0px 10px;
  }
  .HQbuttonText {
    color: rgba(255, 255, 255, 0.65);
    font-size: 1em;
    /*left   : -15px;*/
  }
}
@media only screen and (max-height: 400px) {
  .HQtimelineBox {
    height: calc(100vh - 100px);
  }
}
