.App {
  width: 100%;
  height: 100%;
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-size: 1em;
  color: rgba(255, 255, 255, 0.45);
  background-color: #001529;
}
.APmenu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.APoffset {
  height: 47px;
}
.APhqSelector {
  width: 100%;
  height: 100%;
}
.APtimelineContainer {
  overflow-y: scroll;
  height: 90%;
  padding-bottom: 100px;
}
