@timelineHeight   : 43vh;
@timelineBoxHeight: calc(43vh + 50px);

.HQcontainer {
  width             : 100%;
  height            : 100%;
  // height         : calc(100% - 100px);
  display           : flex;
  // justify-content: center;
  justify-content   : flex-start;
  flex-direction    : column;
  // position       : absolute;
  // bottom         : 0px;  
  // background     : @loGray;
}

.HQtopContainer {
  // min-height   : 200px;
  // height       : 100%;
  height          : calc(100% - @timelineBoxHeight);
  // height       : 500px;
  width           : 100%;
  display         : flex;
  flex-wrap       : nowrap;
  flex-shrink     : 0;
  align-items     : center;
  justify-content : space-evenly;
  padding         : 15px 45px 15px 55px;
  background-color: fade(white, 3%);
  border-bottom   : 1px solid fade(white, 10%);
}

.HQconsole {
  // background: pink;
  margin       : 0px 35px 0px 0px;
  width        : 45vw;
  height       : 90%;
}

.HQuiControls {
  width           : 45vw;
  max-width       : 550px;
  height          : 90%;
  display         : flex;
  flex-wrap       : nowrap;
  align-items     : center;
  border-radius   : 5px;
  // background      : rgba(0, 0, 0, 0.55);
  // border       : 5px solid fade(black, 50%);  
  // min-width    : 400px;
}

.HQtimelineBox {
  width        : 100%;
  padding      : 5px 20px 10px 5px;
  display      : block;
  flex-wrap    : nowrap;
  flex-shrink  : 0;
  height       : @timelineHeight;
  position     : absolute;
  bottom       : 0px;
  // background: pink;
  // margin-top: 10px;
  // background: fade(white, 3%);
}

.HQtimelineButtonRow {
  padding  : 5px 20px 5px 55px;
  // position : relative;
  // top      : 2px;
  flex-wrap: nowrap;
}

.HQtimelineButtonUnit {
  padding            : 0px 12px 0px 12px;
  // background-color: yellow;
}

.HQbuttonText {
  color    : @hiGray;
  font-size: 1.2em;
  /*left   : -15px;*/
}


@media only screen and (max-width: 800px) {
  .HQcontainer {
    position       : static;
    justify-content: flex-start;
  }

  .HQtopContainer {
    padding         : 10px 0px 10px 0px;
    justify-content : center;
    // background-color: fade(black, 50%);
  }

  .HQuiControls {
    width: 100%;
  }

  .HQtimelineButtonRow {
    padding: 0px 5px 5px 10px;
  }

  .HQtimelineButtonUnit {
    padding: 0px 0px 0px 10px;
  }

  .HQbuttonText {
    color    : @hiGray;
    font-size: 1em;
    /*left   : -15px;*/
  }
  
}

@media only screen and (max-height: 400px) {
  .HQtimelineBox {
    height: calc(100vh - 100px);
  }
}

