.UIcontrols {
  flex-wrap: nowrap;
  flex-shrink: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: space-evenly;
  padding: 20px 0px 20px 20px;
  max-height: 350px;
  min-height: 190px;
  color: rgba(255, 255, 255, 0.65);
}
.UIpinBox {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 80px;
  height: 100%;
}
.UIpinUnit {
  padding: 0px 0px 0px 0px;
  min-width: 80px;
  vertical-align: middle;
  /*background-color: green;*/
}
.UIvSliderUnit {
  float: left;
  height: calc(100% + 5px);
  min-height: 190px;
  width: 40px;
  text-align: center;
}
.UIvSliderTitle {
  position: relative;
  top: -7px;
}
.UIvSliderBox {
  display: flex;
  flex-wrap: nowrap;
  height: 100%;
  min-width: 90px;
}
.UIdialBox {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-end;
  text-align: center;
  padding: 0px 0px 0px 10px;
}
.UIcaptureIconUnit {
  display: block;
  margin: auto;
  font-size: 1.75em;
  position: relative;
  top: 5px;
}
.UImotorId {
  color: rgba(255, 255, 255, 0.65);
  font-size: 1.75em;
  position: relative;
  top: 8px;
}
@media only screen and (max-width: 600px) {
  .UIcontrols {
    padding: 0px 20px 0px 20px;
    max-height: 250px;
  }
  .UIcaptureIconUnit {
    font-size: 1.5em;
  }
  .UImotorId {
    font-size: 1.5em;
    position: relative;
    top: 10px;
  }
  .UIvSliderUnit {
    width: 30px;
  }
  .UIdialBox {
    position: relative;
    left: 0px;
  }
}
